.container {
  text-align: center;
  margin-top: 50px;
}

.breed-select {
  margin-bottom: 10px;
  padding: 5px;
  font-size: 16px;
}

.loader {
  margin-top: 20px;
  font-size: 18px;
  display: none;
}

.error {
  color: red;
  margin-top: 20px;
  font-size: 18px;
  display: none;
}

.cat-info {
  opacity: 0;
  background-color: #f1f1f1;
  border-radius: 5px;
  align-items: center;
  max-width: 50%;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  transition: opacity .3s;
  display: flex;
}

.cat-info.visible {
  opacity: 1;
}

.cat-info img {
  max-width: 50%;
  height: auto;
  margin-right: 10px;
}

.cat-info-content {
  flex-grow: 1;
}

.cat-info h2 {
  margin-top: 0;
  font-size: 24px;
}

.cat-info p {
  margin-top: 5px;
  font-size: 18px;
}

